import {Row, Col} from 'reactstrap'

const Game = ({data}) => {
  const wrapper_class = (data.index - 1) % 2 ? "col-12 flex m-t-40" : "col-12 flex m-t-40 flex-reverse"
  return (
    <>
      <Row className={wrapper_class}>
          <Col md={6} sm={12}>
            <img className="w-100" src={data.url} alt={data.title} title={data.title} />
          </Col>
          <Col md={6} sm={12} className="m-t-20">
            <h4>{data.title}</h4>
            <p>{data.content}</p>
            <h6>CLICK HERE FOR COMPETITION RULES AND TO START A GAME</h6>
          </Col> 
      </Row>
    </>
  )
}

export default Game